.app-header-explore {

    display: flex;
    align-items: center;
    height: 80px;
    width: 100%;
    font-size: 16px;

    .logo {
        font-size: 2em;
        letter-spacing: -1.5px;
        font-family: Work Sans Bold;
        font-size: 30px;
        padding-bottom: 7px;
    }

    .header-search {
        flex-grow: 1;
        margin: 0 30px;

        .header-input {
            width: 80%;
            padding: 10px 2px 10px 16px;
            border: 1px $header-input-gray solid;
            border-radius: 4px 0 0 4px;
            font-size: 18px;
            font-weight: 400;
        }

        .header-search-btn {
            background-color: $black;
            padding: 8px 15px;
            border-radius: 0 4px 4px 0;
            border-color: transparent;
        }

        .header-search-btn:hover {
            opacity: 0.9;
        }
    }

    .header {
        height: 100%;
    }

    .header-nav {
        .header-join {
            :hover {
                a {

                    color: white !important;
                }
            }
        }
    }

    .header-nav>a {
        padding: 0 10px;
        font-weight: 600;
        color: $bright-gray;
        transition: 0.2s;
    }

  



    span {
        color: $bright-green;
        font-size: 45px;
    }

    .header-join {
        padding:0px 16px;
       
        background-color: transparent;
        border: none;
        font-weight: 600;
        font-size: 14px;
        border: 1px $bright-green solid;
        border-radius: 4px;
        margin-right: 1px;
        transition: 0.2s;
        cursor: pointer;
        a {
            color: $bright-green;
        }

        :hover {
            color: white;
        }
    }

   

    a:hover {
        color: $bright-green;
    }

    a.active:not(:last-child) {
        color: $bright-green;
    }

    .header-join:hover {
        background-color: $bright-green;
    }

    
    .header-signin {
    
        font-weight: 600;
        cursor: pointer;
        color: $bright-gray;
        padding: 0 10px;

        .header-signin :hover {
            color: $bright-green;
        }
    }
}